import Vuex from 'vuex';

export default new Vuex.Store({
    state: {
        products: [{
                id: 1,
                name: "Airpods",
                price: "199",
                img: "airpods.jpg",
                desc: "Airpords detail"
            },
            {
                id: 2,
                name: "Air Jordans",
                price: "125",
                img: "air-jordans.jpg",
                desc: "Air Jordans detail"
            },
            {
                id: 3,
                name: "Smartphone",
                price: "699",
                img: "smartphone.jpg",
                desc: "Smartphone detail"
            }
        ],

        cartProducts: [],
        currentProduct: {},
        showModal: false,
        showPopupCart: false,
    },

    getters: {
        getAllProducts: state => state.products,
        getProductsInCart: state => state.cartProducts,
        getCurrentProduct: state => state.currentProduct,
        getProductById: (state) => (id) => { return state.products.find(product => product.id == id); },
        getImgUrl: (state) => (id) => { return require('./assets/images/' + state.products.find(product => product.id == id).img); },
    },

    mutations: {
        ADD_PRODUCT: (state, product) => {
            state.cartProducts.push(product);
        },
        REMOVE_PRODUCT: (state, index) => {
            state.cartProducts.splice(index, 1);
        },
        CURRENT_PRODUCT: (state, product) => {
            state.currentProduct = product;
        },
        SHOW_MODAL: (state) => {
            state.showModal = !state.showModal;
        },
        SHOW_POPUP_CART: (state) => {
            state.showPopupCart = !state.showPopupCart;
        },
    },

    actions: {
        addProduct: (context, product) => {
            context.commit('ADD_PRODUCT', product);
        },
        removeProduct: (context, index) => {
            context.commit('REMOVE_PRODUCT', index);
        },
        currentProduct: (context, product) => {
            context.commit('CURRENT_PRODUCT', product);
        },
        showOrHiddenModal: (context) => {
            context.commit('SHOW_MODAL');
        },
        showOrHiddenPopupCart: (context) => {
            context.commit('SHOW_POPUP_CART');
        },
    },
});