<template>
    <div id="navbar">
        <span v-if="isLoggedIn" id="logout">
            <a  @click="logout"> <b>Logout </b> </a>
        </span>
    </div>
</template>

<script>
export default {
    name: 'NavBar',
    computed : {
        isLoggedIn : function(){ return localStorage.getItem('user');}
    },
    methods: {
        logout (){
            localStorage.removeItem('user');
            this.$router.push('/login')
        }
  }
}
</script>
<style scoped>
    #navbar {
        display: flex;
        justify-content: flex-end;
    }
</style>
